.n-wrapper{
    height : 10vh;
    display: flex;
    justify-content: space-between;
}
.n-left{
    display : flex;
   flex : 1;
   align-items: center;
   gap : 2rem;
}
.n-name{
    font-weight: bold;
    font-size: 1.3rem;
}
.n-right{
    display: flex;
    flex : 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list{
    flex:10;
}
.n-list>ul{
    display: flex;
    gap : 2rem;
    margin-right: 2rem;
}
.n-list>ul>li:hover{
    cursor: pointer;
    color : var(--orange)
}
.n-button{
    flex : 2
}