.intro{
   display: flex;
   height: 77vh;
   margin-top:6rem ;
}
.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    gap:2rem
}
.i-name{
    display: flex;
    flex-direction: column;
}
.i-name>:nth-child(1){
    color:var(--black);
    font-size: 3rem;
    font-weight: bold;
}
.i-name>:nth-child(2){
    color:var(--orange);
    font-size: 3rem;
    font-weight: bold;
}
.i-name>:nth-child(3){
    color:var(--gray);
    font-size: 15px;
    font-weight: 200;
    margin-top: 20px;
}
.i-button{
    height:2rem;
    width: 6rem;
}
.i-icon{
    margin-top: 3rem;
    display:flex;
    gap:0rem;
}
.i-icon>*{
    transform: scale(0.5);
}
.i-icon>*:hover{
    cursor: pointer;
}
.i-right{
    flex:1;
    position:relative;
}
.i-right>*{
    position:absolute;
    z-index:1
}
.i-right>:nth-child(1){
    transform: scale(0.69);
    left:-15%;
    top:-9%;
}
.i-right>:nth-child(2){
    top:-4.6rem;
    transform:scale(0.67);
    left:-3rem
}
.i-right>:nth-child(3){
    transform:scale(1.4);
    left:28%
}
.i-right>:nth-child(4){
    transform: scale(0.6);
    top:-19%;
    left:-24%;
    border-radius:50%;
    padding:0
}
.blur{
    position:absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter:blur(72px);
    z-index: -9;
    top:-18%;
    left:56%
}